.header {
    min-height: 100px;
    padding: 0 0 5px;
    margin: 0 15px 15px;
    border-bottom: 1px solid #333333;
}

.header img {
    height: 100px;
    margin: -12px 0 0 -20px;
}

.registration-links {
    float: right;
}

.registration-links span {
    margin: 0 0 0 10px;
    position: relative;
    top: 65px;
    color: blue;
    cursor: pointer;
}

.registration-links span img {
    width: 20px;
    height: 20px;
    margin: -2px 5px 0;
}