.story-block {
    border: 1px solid #333333;
    text-align: center;
    padding: 15px;
}

.story-block h4 {
    text-align: center;
    text-decoration: underline;
}

.story-block .story-lines {
    text-align: left;
    min-height: 120px;
}

.story-block .read-more-button {
    margin: 10px 0 0;
}

.story-image-container {
    height: 100px;
    width: 100px;
    border: 1px solid #333333;
    margin: 0 10px 0 0;
    float: left;
}

.story-image-container img {
    height: 98px;
    width: 98px;
}